import bottomModal from '../bottomModal/bottom-modal.vue'
export default {
	props: {
		isShowModal: {
			type: Boolean
		},
		tips: {
			type: String,
			default() {
				return '授权校服务平台'
			}
		}
	},
	components: {
		bottomModal
	},
	methods: {
		ongetuserinfo(res) {
			if(res.detail.userInfo) {
				this.$emit('userGreeGivePower', res.detail.userInfo)
			}else {
				this.$emit('userRejectGivePower')
			}
		},
		onbottomModalclosed() {
			this.$emit('bottomModalclosed')
		}
	}
}
